@import 'styles/utils';

.baseButton {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 4px;
  padding: 0;

  border: 0;
  border-radius: 20px;
  background: transparent;

  color: $c-white;
  text-align: center;

  cursor: pointer;
}

.primary {
  @extend %t-inter-13-medium;

  background: $c-green;
  border: 1px solid $c-white-02
}

.secondary {
  @extend %t-inter-13-medium;

  background: $c-purple;

}

.border {
  @extend %t-inter-13-medium;

  background: $lg-brown;
  border: 1px solid $c-white-02;

  .circle {
    border-radius: 50%;
    padding: 0px;
  }
}

.lite {
  background: $c-white-01;
  border-radius: 4px;
  color: $c-white-06;

  .active {
    background: $c-purple
  }
}

.simple {
  @extend %t-inter-11-normal;

  color: inherit;
}

.small {
  height: 24px;
  padding: 4px 8px;
}

.medium {
  height: 32px;
  padding: 8px 12px;
}

.large {
  height: 40px;
  padding: 12px 16px;
}
