@import 'styles/utils';

.connectWalletBtn {
  @extend %t-inter-10-bold-upper;

  background: $c-white;
  color: $c-orange;

  &:hover {
    box-shadow: 0px 0px 7px $c-white;

    .connectWalletBtnTitle {
      background: $lg-orange;
      background-clip: text;
      color: transparent;
    }
  }
}

.extBtn {
  @extend %t-inter-10-bold-upper;

  padding: 0 48px 0 48px;

  background: $c-blue;
  border: none;

  &:hover {
    background: $c-white;
    box-shadow: 0px 0px 7px $c-white;

    .extBtnTitle {
      background: $lg-orange;
      background-clip: text;
      color: transparent;
    }
  }
}

.chromeBtn {
  @extend %t-inter-10-bold-upper;

  gap: 24px;

  background: $c-white;
  color: $c-lite-blue;
}

.copyBtn {
  transition: color 2s;
}

.copyBtnActive {
  transition: color 0s;
  color: $c-green;
}
