@import 'styles/utils';

$shadow-wallet: (
  0px 16px 24px rgba(0, 0, 0, 0.14),
  0px 6px 30px rgba(0, 0, 0, 0.12),
  0px 8px 10px rgba(0, 0, 0, 0.2),
);

.wallet {
  position: fixed;
  display: flex;
  flex-direction: column;

  padding: 16px 24px;
  width: 368px;

  top: 85px;
  right: 50px;

  border-radius: 12px;
  background: $lg-white;
  box-shadow: $shadow-wallet;

  color: $c-dark-purple;
}

.wrapper {
  display: flex;
  align-items: center;
}

.balance {
  @extend %t-inter-23-normal;

  margin-right: auto;
}

.addressTitle {
  @extend %t-inter-10-bold-upper;

  margin-top: 18px;
}

.address {
  @extend %t-inter-13-medium;
}

.walletBtn {
  @extend %t-inter-13-medium;

  background: $c-lite-red;
  border: 1px solid $c-white-05;
  box-shadow: 0px 2px 24px $c-black-01;

  &:hover {
    background: $c-red;
    border-color: $c-white;
    box-shadow: 0px 2px 24px $c-black-02;
  }
}

.walletBtnInner {
  position: relative;
  display: flex;

  width: 100%;

  &::after {
    position: absolute;
    content: '';
    border-left: 1px solid $c-white-05;
    height: 38px;
    bottom: 50%;
    right: 50%;
    transform: translate(50%, 50%);
  }
}

.walletBtn:hover .walletBtnInner::after {
  border-color: $c-white;
}

.walletBtnAddress {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-right: 8px;
  width: 50%;
}

.walletBtnBalance {
  padding-left: 8px;
  width: 50%;
  white-space: nowrap;
}

