@import "styles/utils";

.header {
  display: flex;
  flex-direction: row;
  align-items: center;

  padding: 16px 0;
}

.icon {
  margin-right: 40px;
  color: $c-white;
}

.navList {
  display: flex;
  padding-top: 4px;
}

.navItem {
  &:not(:last-child) {
    margin-right: 38px;
  }
}

.navLink {
  @extend %t-larsseit-20-normal-upper;

  position: relative;

  color: $c-white-06;

  &:global(.active) {
    color: $c-white;

    &::after {
      position: absolute;
      display: block;
      content: '';
      width: calc(100% - 7px);
      right: 6px;
      bottom: -4px;
      border-bottom: 1px solid $c-white;
    }
  }
}

.wallet {
  display: flex;
  flex-direction: column;
  margin-left: auto;
  width: 208px;
}
