@for $i from 0 through 54 {
  // margins
  .mb-#{$i * 4} {
    margin-bottom: $i * 4px;
  }
  .mt-#{$i * 4} {
    margin-top: $i * 4px;
  }
  .ml-#{$i * 4} {
    margin-left: $i * 4px;
  }
  .mr-#{$i * 4} {
    margin-right: $i * 4px;
  }
  // paddings
  .pb-#{$i * 4} {
    padding-bottom: $i * 4px;
  }
  .pt-#{$i * 4} {
    padding-top: $i * 4px;
  }
  .pl-#{$i * 4} {
    padding-left: $i * 4px;
  }
  .pr-#{$i * 4} {
    padding-right: $i * 4px;
  }
}
