%t-inter-normal {
  font-family: $f-inter-sans;
  font-weight: $font-weight_normal;
}

%t-inter-medium {
  font-family: $f-inter-sans;
  font-weight: $font-weight_medium;
}

%t-inter-bold {
  font-family: $f-inter-sans;
  font-weight: $font-weight_bold;
}

%t-larsseit-normal {
  font-family: $f-larsseit-sans;
  font-weight: $font-weight_normal;
}

%t-inter-11-normal {
  @extend %t-inter-normal;
  font-size: 11px;
  line-height: 13px;
}

%t-inter-23-normal {
  @extend %t-inter-normal;
  font-size: 23px;
  line-height: 32px;
}

%t-inter-13-medium {
  @extend %t-inter-medium;
  font-size: 13px;
  line-height: 16px;
}

%t-inter-10-bold {
  @extend %t-inter-bold;
  font-size: 10px;
}

%t-inter-10-bold-upper {
  @extend %t-inter-10-bold;
  letter-spacing: 2px;
  text-transform: uppercase;
  line-height: 12px;
}

%t-larsseit-20-normal-upper {
  @extend %t-larsseit-normal;
  font-size: 20px;
  line-height: 22px;
  letter-spacing: 5px;
  text-transform: uppercase;
}
